.card-content {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 30px;
  &__left {
    flex: 0 1 auto;
  }

  &__right {
    flex: 0 1 auto;
    width: 600px;
  }
}
.card-slider {
  display: flex;
  &__thumbs {
    flex: 0 0 76px;
    margin-right: 10px;
  }
  &__main {
    padding-right: 20px;
    width: 600px;
  }
}

.card-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 40px;

  &__title {
    font-weight: 500;
    margin-bottom: 8px;
    text-align: center;
  }
  &__color {
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 1.3px;
    margin-bottom: 8px;
    font-weight: 400;
  }
  &__price {
    font-weight: 700;
    font-size: 20px;
    letter-spacing: 1.5px;
  }
}

.card-sizes {
  margin-bottom: 30px;
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__item {
    cursor: pointer;
    display: inline-block;
    padding: 10px 40px;
    border: 1px solid #000;
    text-transform: uppercase;
    &:not(:first-child) {
      border-left: none;
    }
    &.active {
      border: 2px solid #000;
    }
  }
}

.card-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;

  &__cart {
    background-color: #333;
    color: #fff;
    margin-bottom: 10px;
  }
  &__wish {
    background-color: #c2c2c2;
    color: #fff;
  }
}
.product-button {
  display: inline-block;
  font-size: 18px;
  border: none;
  outline: none;
  width: 220px;
  height: 50px;
  text-transform: uppercase;
  cursor: pointer;
}

.card-tabs {
  margin-bottom: 10px;
  &__list {
    display: flex;
    justify-content: space-evenly;
    border-bottom: 1px solid #aaa;
    margin-bottom: 20px;
  }
  &__item {
    flex: 0 0 25%;
    text-align: center;
    justify-self: center;
    position: relative;
    padding: 10px 20px;
    cursor: pointer;
    transition: all 0.2s;
    &.active {
      border-bottom: 2px solid rgb(39, 39, 39);
    }

    span {
      text-align: center;
      text-transform: uppercase;
    }
  }
  &__content {
    padding: 0 20px;
    width: 100%;
  }
}
