.products__toolbar {
  display: flex;
  justify-content: space-between;
  // position: relative;
  margin-bottom: 10px;
  &-view {
    margin-right: 10px;
  }
  &-handlers {
    display: flex;
  }
  &-sort {
    position: relative;
  }
  &-list {
    position: absolute;
    width: 200px;
    top: 20px;
    right: 0;
    // height: 100%;
    z-index: 20;
    border: 1px solid rgb(247, 247, 247);
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    opacity: 0;
    width: 0;
    transition: all 0.3s;

    &.active {
      opacity: 1;
      width: 200px;
    }
  }
  &-item {
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
    text-align: start;
    font-weight: 600;
    &:hover {
      background-color: rgb(247, 247, 247);
    }

    &:first-child {
      border-bottom: 1px solid rgb(247, 247, 247);
    }
  }
}

.btn-toolbar {
  border: none;
  outline: none;
  background: transparent;
  display: flex;
  // align-items: center;
  position: relative;
  & span {
    padding: 0 5px;
  }
  &__icon {
    align-self: center;
  }
}
